#partner {
    background-color: #F9E4B7;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(3, 1fr);
    justify-items: center;
    align-items: center;
}

.partnerWrapper {
    width: 300px;
    height: 300px;
    text-align: center;
}

.partnerImage {
    width: 50%;
}